import React, { PureComponent } from 'react';

import styles from './styles.scss';
import forms from '../../sectionForms';
import { sectionTypes } from '../../constants';

export default class PageSidebar extends PureComponent {
  render() {
    const { currentSection } = this.props;

    console.log(currentSection);

    const FormComponent = forms[currentSection.sectionType];

    const title = currentSection.sectionType === sectionTypes.DEFAULT_SECTION
      ? 'Select section type'
      : 'Update section parameters';

    return (
      <div className={styles.sectionEditor__container}>
        <div className={styles.sectionEditor__top}>
          {title}
        </div>

        <div className={styles.sectionEditor__content}>
          <FormComponent {...this.props} />
        </div>
      </div>
    )
  }
}



