import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';

import styles from './styles.scss';
import sectionViews from '../../sectionViews';
import AddSection from '../../components/AddSection';
import { getEmptySection } from '../../utils';

class Preview extends PureComponent {
  addSection = (index) => {
    this.props.addSection(getEmptySection(), index);
  }

  render() {
    const { className, actions, formValues } = this.props;

    const cl = cx({
      [className]: !!className,
      [styles.preview__container]: true
    });
  
    const sections = formValues ? formValues.pages[0].sections : [];
  
    return (
      <section className={cl}>
        <div className={styles.browser__container}>
          {
            sections.map((item, index) => {
              const SectionComponent = sectionViews[item.sectionType] || sectionViews.DEFAULT_SECTION;
  
              return (
                <div
                  className={styles.section__container}
                  key={item.sectionId}
                >
                  <SectionComponent
                    actions={actions}
                    {...item}
                  />
  
                  {
                    (index !== sections.length - 1) &&
                    <AddSection onClick={() => this.addSection(index)} />
                  }
                </div>
              );
            })
          }
        </div>
      </section>
    );  
  }
};

Preview.propTypes = {

}

export default Preview



