import React from 'react'
import { string } from 'prop-types'
import cx from 'classnames';

import styles from './styles.scss';

const Sidebar =  ({ children, className }) => {
  const cl = cx({
    [styles.sidebar__container]: true,
    [className]: !!className
  });

  return (
    <aside className={cl}>
      {children}
    </aside>
  )
}

Sidebar.propTypes = {
  className: string
}

export default Sidebar;


