import React from 'react';
import { string, shape } from 'prop-types';

import styles from './styles.scss';

const About = ({ params, actions, sectionId }) => {
  const {
    title,
    description,
    blocks,
    backgroundColor,
    fontColor
  } = params;

  const style = {
    backgroundColor,
    fontColor
  };

  const headerStyle = {
    color: fontColor
  };

  const { setEditPageMode } = actions;

  return (
    <section
      style={style}
      className={styles.container}
      onClick={() => setEditPageMode(0, sectionId)}
    >
      <h2 style={headerStyle}>
        {title}
      </h2>

      <p>
        {description}
      </p>
    </section>
  )
}

About.propTypes = {
}

export default About;



