import { createSelector } from 'reselect';
import { bindActionCreators } from 'redux';
import { arraySplice, arrayRemove, change } from 'redux-form';

import { modes, EDITOR_FORM, TEST_SECTION } from './constants';

export const moduleName = 'editor';

const SET_SITE = `${moduleName}/SET_SITE`;
const SET_EDIT_PAGE_MODE = `${moduleName}/SET_EDIT_PAGE_MODE`;

// Initial state
const defaultState = {
  activePageId: null,
  activeSectionId: null,
  mode: modes.EDIT_SITE,
  isLoading: true,
  site: null
}

// Action Creators
export const setSite = ({ site }) => {
  return {
    type: SET_SITE,
    payload: { ...site }
  };
};

export const setEditPageMode = (pageId, sectionId) => {
  return {
    type: SET_EDIT_PAGE_MODE,
    payload: { pageId, sectionId }
  };
};

const actions = {
  setSite,
  setEditPageMode
};

const reducer = (state = defaultState, action = {}) => {
  switch (action.type) {
    case SET_SITE:
      return {
        ...state,
        site: action.payload,
        isLoading: false
      }
    case SET_EDIT_PAGE_MODE:
      const { payload } = action;
      return {
        ...state,
        mode: modes.EDIT_PAGE,
        activePageId: payload.pageId,
        activeSectionId: payload.sectionId
      }
    default:
      return state;
  }
}

// Selectors

const getActivePage = (state) => {
  if (!state.formState) {
    return null;
  }

  if (!state.formState[EDITOR_FORM]) {
    return null;
  }

  // return state[moduleName].pages.find(item => item.pageId === state[moduleName].activePageId);
  return state.formState[EDITOR_FORM].values.pages[state[moduleName].activePageId];
};

const getSectionById = (page, id) => {
  if (!page) {
    return null;
  }
  return page.sections.find(item => item.sectionId === id)
};

const getSectionIndex = (page, id) => {
  if (!page) {
    return null;
  }
  let result = null;
  page.sections.forEach((item, index) => {
    if (item.sectionId === id) {
      result = index;
    }
  })
  return result;
};

const getCurrentSection = createSelector(
  state => getActivePage(state),
  state => state[moduleName].activeSectionId,
  (page, id) => getSectionById(page, id)
);

const getCurrentSectionIndex = createSelector(
  state => getActivePage(state),
  state => state[moduleName].activeSectionId,
  (page, id) => getSectionIndex(page, id)
);

// mapStateToProps
export const mapStateToProps = state => {
  return {
    ...state[moduleName],
    formValues: state.formState.EDITOR_FORM && state.formState.EDITOR_FORM.values,
    currentSection: getCurrentSection(state),
    currentSectionIndex: getCurrentSectionIndex(state)
  };
};

// mapDispatchToProps

export const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(actions, dispatch),
  addSection: (section, num) => dispatch(arraySplice(EDITOR_FORM, 'pages[0].sections', num - 1, null, section)),
  setSectionType: (section, num, sectionId) => {
    const newSection = {
      ...TEST_SECTION,
      sectionId
    }

    return dispatch(change(EDITOR_FORM, `pages[0].sections[${num}]`, newSection));
  }
});

// export module object

export default {
  moduleName,
  reducer
};



