import {
  initialize,
  getFormValues,
  getFormSyncErrors,
  startSubmit,
  setSubmitFailed,
  stopSubmit,
  change
} from 'redux-form';

import { EDITOR_FORM, TEST_SITE } from './constants';
import {
  setSite
} from './index';
import { dispatch, getState } from '../../';

export const loadSite = (siteId) => {
  setTimeout(() => {
    dispatch(initialize(EDITOR_FORM, { ...TEST_SITE } ));
    dispatch(setSite({ site: TEST_SITE }));
  }, 1000);
};



