import React from 'react';
import { string, shape } from 'prop-types';
import { Field } from 'redux-form';

import TextEditor from '../../components/AppInput';

import styles from './styles.scss';

const About = ({ currentSectionIndex: index }) => {
  return (
    <section
      className={styles.container}
    >
      <Field
        name={`pages[0].sections[${index}].params.title`}
        component={TextEditor}
        label={'Title'}
      />

      <Field
        name={`pages[0].sections[${index}].params.description`}
        component={TextEditor}
        label={'Description'}
      />

      <Field
        name={`pages[0].sections[${index}].params.backgroundColor`}
        component={TextEditor}
        label={'Background color'}
      />

      <Field
        name={`pages[0].sections[${index}].params.fontColor`}
        component={TextEditor}
        label={'Header font color'}
      />
    </section>
  )
}

About.propTypes = {
}

export default About;



