import React from 'react';
import { string, shape } from 'prop-types';

import styles from './styles.scss';

const Footer = ({ params, actions, sectionId }) => {
  const { leftText, rightText, backgroundColor } = params;

  const { setEditPageMode } = actions;

  const pageId = 0;

  const containerStyle = {
    backgroundColor
  };

  return (
    <section
      style={containerStyle}
      className={styles.container}
      onClick={() => setEditPageMode(pageId, sectionId)}
    >
      <p>
        {leftText}
      </p>

      <p>
        {rightText}
      </p>
    </section>
  )
}

Footer.propTypes = {
}

export default Footer;



