import React from 'react';
import { FormGroup, ControlLabel, FormControl } from 'react-bootstrap';

const TextEditor = ({
  input,
  label,
  meta: { touched, error },
}) => {
  return (
  <FormGroup>
    <ControlLabel>{label}</ControlLabel>
    <FormControl
      type="text"
      placeholder="Enter value"
      {...input}
    />
  </FormGroup>
  );
}

export default TextEditor;



