import React from 'react';
import { string, shape } from 'prop-types';
import { Field } from 'redux-form';

import TextEditor from '../../components/AppInput';
import styles from './styles.scss';

const Footer = ({ currentSectionIndex: index }) => {
  return (
    <section
      className={styles.container}
    >
      <Field
        name={`pages[0].sections[${index}].params.leftText`}
        component={TextEditor}
        label={'Copyright'}
      />

      <Field
        name={`pages[0].sections[${index}].params.rightText`}
        component={TextEditor}
        label={'Powered by'}
      />
    </section>
  )
}

Footer.propTypes = {
}

export default Footer;



