import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { reduxForm, Form } from 'redux-form';

import { mapStateToProps, mapDispatchToProps } from './index';
import { loadSite } from './services';
import { EDITOR_FORM, modes } from './constants';

import Header from '../layout/components/Header';
import Sidebar from '../layout/components/Sidebar';
import Preview from './containers/Preview';
import SiteSidebar from './containers/SiteSidebar';
import SectionSidebar from './containers/SectionSidebar';
import styles from './styles.scss';

class EditorPage extends PureComponent {
  componentDidMount = () => {
    loadSite();
  }

  handleSubmit = () => {
    console.log('submitted');
  }
  
  render() {
    const { pages, mode } = this.props;

    let SidebarComponent;
    switch (mode) {
      case modes.EDIT_PAGE:
        SidebarComponent = SectionSidebar;
        break;
      case modes.EDIT_SITE:
        SidebarComponent = SiteSidebar;
        break;
    }

    return (
      <div className={styles.editor__container}>
        <Header className={styles.header}>
          {'Editor'}
        </Header>
  
        <div className={styles.content}>
          <Preview
            className={styles.preview}
            {...this.props}
          />
  
          <Sidebar className={styles.sidebar}>
            <Form onSubmit={this.handleSubmit}>
              <SidebarComponent {...this.props} />
            </Form>
          </Sidebar>
        </div> 
      </div>
    )
  }
};

EditorPage.propTypes = {

}

const EditorConnectedToReduxForm = reduxForm({
  form: EDITOR_FORM,
  getFormState: reduxState => reduxState.formState,
})(EditorPage);

export default connect(mapStateToProps, mapDispatchToProps)(EditorConnectedToReduxForm);



