import React, { PureComponent } from 'react';

import MenuItem from '../../components/MenuItem';
import styles from './styles.scss';

export default class componentName extends PureComponent {
  render() {
    return (
      <div className={styles.siteEditor__container}>
        <div className={styles.siteEditor__top}>
          {'My website'}
        </div>

        <MenuItem onClick={() => console.log('Edit site item clicked')}>
          {'Theme'}
        </MenuItem>

        <MenuItem onClick={() => console.log('Edit site item clicked')}>
          {'Pages'}
        </MenuItem>

        <MenuItem onClick={() => console.log('Edit site item clicked')}>
          {'Site Settings'}
        </MenuItem>
      </div>
    )
  }
}



