import React from 'react';
import { string, shape } from 'prop-types';

import styles from './styles.scss';

const Top = ({ params, actions, sectionId }) => {
  const { title, backgroundColor } = params;

  const { setEditPageMode } = actions;

  const pageId = 0;

  const containerStyle = {
    backgroundColor
  };

  return (
    <section
      style={containerStyle}
      className={styles.container}
      onClick={() => setEditPageMode(pageId, sectionId)}
    >
      <h1>{title}</h1>
    </section>
  )
}

Top.propTypes = {
}

export default Top



