import React from 'react';
import { string, shape } from 'prop-types';
import { Field } from 'redux-form';

import TextEditor from '../../components/AppInput';

import styles from './styles.scss';

const Top = ({ title }) => {
  return (
    <section
      className={styles.container}
    >
      <Field
        name={'pages[0].sections[0].params.title'}
        component={TextEditor}
        label={'Title'}
      />

      <Field
        name={'pages[0].sections[0].params.backgroundColor'}
        component={TextEditor}
        label={'Background color'}
      />
    </section>
  )
}

Top.propTypes = {
}

export default Top;



